'use strict';

(function () {
  const wordInDrug_n_DropTask = document.querySelector('.modal-container__question--drug_n_drop');
  const mover1 = document.querySelector('.mover1');
  const mover2 = document.querySelector('.mover2');
  const mover3 = document.querySelector('.mover3');
  const mover4 = document.querySelector('.mover4');
  const mover5 = document.querySelector('.mover5');
  const mover6 = document.querySelector('.mover6');


  let currentWord = window.enemyCreate.getRandomElementFromArray(window.baseСonfig.drugndropArray).split('');
  let srtResult = currentWord.join('');
  let shufleWord = shuffle(currentWord);

  mover1.textContent = shufleWord[0];
  mover2.textContent = shufleWord[1];
  mover3.textContent = shufleWord[2];
  mover4.textContent = shufleWord[3];
  mover5.textContent = shufleWord[4];
  mover6.textContent = shufleWord[5];

  function dragWord(dragEvent) {
    dragEvent.dataTransfer.setData("text/html",
    dragEvent.target.textContent + "|" + dragEvent.target.parentNode.id);
  }

  function dropWord(dropEvent) {
    let dropData = dropEvent.dataTransfer.getData("text/html");
    let dropItems = dropData.split("|");
    let prevElem = document.getElementById(dropItems[1]);
    prevElem.getElementsByTagName("div")[0].textContent = dropEvent.target.textContent;
    dropEvent.target.textContent = dropItems[0];
    dropEvent.preventDefault();
  }

  function shuffle(array) {
    let currentIndex = array.length, temporaryValue, randomIndex;
    while (0 !== currentIndex) {
      randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex -= 1;
      temporaryValue = array[currentIndex];
      array[currentIndex] = array[randomIndex];
      array[randomIndex] = temporaryValue;
    }
    return array;
  }

  // функция проверки задачи на вычисление
  function checkDrug_n_DropTask() {
    if (srtResult == (mover1.textContent + mover2.textContent + mover3.textContent + mover4.textContent + mover5.textContent + mover6.textContent)) {
      window.modalContainer.resultOfTask.innerHTML = 'ВЕРНО!';
      window.modalContainer.resultOfTask.classList.add('modal-container__right-result');
      //урон врагу
      setTimeout(function() {
        window.mechanicsGame.changelifeLineCharacter(window.mechanicsGame.increaseHealthForCharacter); // пополненние ХП при выборе заклинания Лечения
        window.boomAnimation.boomAnimationEnemy(); // взрыв после закрытия модалки
        window.mechanicsGame.changelifeLineEnemy(window.mechanicsGame.damageFromEnemy);
      }, 3000);

      // ответный удар врага
      setTimeout(function() {
        // TODO - вызов модалки "ответный удар врага"
        window.boomAnimation.boomAnimationHero(); // взрыв после закрытия модалки
        window.mechanicsGame.changelifeLineCharacter(window.mechanicsGame.damageFromEnemy);
        // TODO - вызов условия подсчета результатов,
        // если победа - поздравления, или идем дальше вызывая след уровень:
        //window.logicGame.getNextLevelTask();
        //console.log(window.logicGame.currentTaskIndex);
        window.logicGame.currentTaskIndex = 3;
        window.popups.activateBtnsInGame(); // кнопки активация
      }, 6000);
    }
    else {
      window.modalContainer.resultOfTask.innerHTML = 'НЕВЕРНО!';
      // ответный удар врана
      setTimeout(function() {
        // TODO - вызов модалки "ответный удар врага"
        window.boomAnimation.boomAnimationHero(); // взрыв после закрытия модалки
        window.mechanicsGame.changelifeLineCharacter(window.mechanicsGame.damageFromEnemy);
        // TODO - вызов условия подсчета результатов,
        // если победа - поздравления, или идем дальше вызывая след уровень:
        //window.logicGame.getNextLevelTask();
        //console.log(window.logicGame.currentTaskIndex);
        window.logicGame.currentTaskIndex = 3;
        window.popups.activateBtnsInGame(); // кнопки активация
      }, 3000);
    }
  }

  window.drug_n_DropTask = {
    checkDrug_n_DropTask: checkDrug_n_DropTask,
    dragWord: dragWord,
    dropWord: dropWord
  };
})();
