'use strict';

(function () {
  const btnForAuditionTask = document.querySelector('.modal-container__btn-speak');

  let randomArrayForAudition = window.enemyCreate.getRandomElementFromArray(window.dictionary.arrayWordsForAudition);
  let arrayOfRightAnswersForAudition = randomArrayForAudition.slice(1); // массив правильных ответов
  //console.log('Варианты правильных ответов на аудирование: ' + arrayOfRightAnswersForAudition);

  let wordForAudition = new SpeechSynthesisUtterance();
  wordForAudition.text = randomArrayForAudition[0];

  // функция вставки значений для аудирования
  function speakHandler(startOver) {
    speechSynthesis.cancel();
    if (startOver) {
      speechSynthesis.speak(wordForAudition);
    }
  }
  btnForAuditionTask.addEventListener('click', speakHandler);

  // функция проверки задачи на вычисление
  function checkAuditionTask() {
    if (window.modalContainer.inputAnswerForTask.classList.contains('answered')) {
      if (arrayOfRightAnswersForAudition.includes(window.modalContainer.inputAnswerForTask.value)) {
        window.modalContainer.resultOfTask.innerHTML = 'ВЕРНО!';
        window.modalContainer.resultOfTask.classList.add('modal-container__right-result');
        //урон врагу
        setTimeout(function() {
          window.mechanicsGame.changelifeLineCharacter(window.mechanicsGame.increaseHealthForCharacter); // пополненние ХП при выборе заклинания Лечения
          window.mechanicsGame.changelifeLineEnemy(window.mechanicsGame.damageFromEnemy);
          window.boomAnimation.boomAnimationEnemy(); // взрыв после закрытия модалки
        }, 3000);

        // ответный удар врага
        setTimeout(function() {
          // TODO - вызов модалки "ответный удар врага"
          window.boomAnimation.boomAnimationHero(); // взрыв после закрытия модалки
          window.mechanicsGame.changelifeLineCharacter(window.mechanicsGame.damageFromEnemy);
          // TODO - вызов условия подсчета результатов,
          // если победа - поздравления, или идем дальше вызывая след уровень:
          //window.logicGame.getNextLevelTask();
          //console.log(window.logicGame.currentTaskIndex);
          window.logicGame.currentTaskIndex = 0;
          window.popups.activateBtnsInGame(); // кнопки активация
         }, 6000);
      }
      else {
        window.modalContainer.resultOfTask.innerHTML = 'НЕВЕРНО!';
        // ответный удар врага
        setTimeout(function() {
          // TODO - вызов модалки "ответный удар врага"
          window.boomAnimation.boomAnimationHero(); // взрыв после закрытия модалки
          window.mechanicsGame.changelifeLineCharacter(window.mechanicsGame.damageFromEnemy);
          // TODO - вызов условия подсчета результатов,
          // если победа - поздравления, или идем дальше вызывая след уровень:
          //window.logicGame.getNextLevelTask();
          //console.log(window.logicGame.currentTaskIndex);
          window.logicGame.currentTaskIndex = 0;
          window.popups.activateBtnsInGame(); // кнопки активация
        }, 3000);
      }
    }
  }

  window.auditionTask = {
    checkAuditionTask: checkAuditionTask
  };
})();
